export default {
	data() {
		return {
            many:'0',
            tabCurrentIndex:0,
            contion:'',
            daker:'',
            lornd:'',
            kolo:'',
            flag:false,
            navList: [
            	{
            		state: 0,
            		text: '待付款'
            	},
            	{
            		state: 1,
            		text: '待确认'
            	},
            	{
            		state: 2,
            		text: '待审核'
            	},
                {
                    state: 3,
                    text:  '已完成'
                }
            ]
		};
    },
    onShow(options) {
        var that=this;
        this.getData()
        uni.request({
            url:this.url + 'ordernum/1',//获取订单数api
            method:'GET',
            header:{
                Authorization: 'JWT'+' '+this.global_.token
            },
            success(res) {
                that.many = res.data.data
            }
        })
    },
    methods:{
        getData(){
            var that=this;
            uni.request({
                url:this.url + 'buyall/101',//获取101的数据
                method:'GET',
                header:{
                     Authorization: 'JWT'+' '+this.global_.token
                },
                success(res) {
                    var contion = res.data.data
                    that.contion = contion.reverse()
                }
            })
        },
        tabClick:function (index) {
            var that = this
            if (this.tabCurrentIndex === index) {
                return false
            }else {
                that.tabCurrentIndex =index
            }
            if(this.tabCurrentIndex === 1) {
                uni.request({
                    url:this.url + 'buyall/102',//获取102的数据
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var kolo = res.data.data
                        that.kolo = kolo.reverse()
                    }
                })
            }
            if(this.tabCurrentIndex === 2) {
                uni.request({
                    url:this.url + 'buyall/103',//获取103的数据
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var lornd = res.data.data
                        that.lornd = lornd.reverse()
                    }
                })
            }
            if(this.tabCurrentIndex === 3) {
                uni.request({
                    url:this.url + 'buyall/104',//获取104的数据
                    method:'GET',
                    header:{
                        Authorization: 'JWT'+' '+this.global_.token
                    },
                    success(res) {
                        var daker = res.data.data
                        that.daker = daker.reverse()
                    }
                })
            }
        },
        btn:function (item) {
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    var ord = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:"../staypay/staypay?mvvp=" + ord
                    })
                }
            })
        },
        bt:function(item) {
            var that = this
            uni.request({
                url:this.url + 'ordercancel/',
                method:'POST',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    if(res.statusCode == 200) {
                        uni.showToast({
                            title:'取消成功',
                            icon:'none',
                            duration:2000
                        })
                    }else if(res.statusCode == 400) {
                        uni.showToast({
                            title:'该订单不可删除',
                            icon:'none',
                            duration:2000
                        })
                    }
                    that.getData()
                }
            })
        },
        btn2:function(item) {
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    var supers = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:'../stayconfirm/stayconfirm?main=' + supers
                    })
                }
            })
        },
        btn3:function (item) {
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    var doink = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:'../stayaudit/stayaudit?dospp=' + doink
                    })
                }
            })
        },
        btn4:function(item){
            var that = this
            uni.request({
                url:this.url + 'salemessage/',
                method:'GET',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    order_num:item.order_num
                },
                success(res) {
                    var blone = JSON.stringify(res.data.data)
                    uni.navigateTo({
                        url:'../completed/completed?deattr=' + blone
                    })
                }
            })
        }
    }
}
